:root {
  --main-color: #3d4e61;
  --border-color: #e1e7ed;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf") format("truetype");
  font-weight: 700;
  font-style: bold;
}

body {
  margin: 0;
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

.search-form {
  margin-top: 20px;
}

.search-form__input {
  border: 1px solid var(--border-color);
  box-sizing: border-box;
  border-radius: 3px;
  outline: none;
  width: 100%;
  font-family: inherit;
}

.search-form__input::placeholder {
  color: var(--main-color);
}

.search-form__input:focus::placeholder {
  color: #0000;
}

.search-form__input--big {
  height: 70px;
  padding: 25px 0 26px 62px;
  font-size: 16px;
  line-height: 22px;
}

.search-form__input--small {
  height: 42px;
  padding: 11px 0 12px 32px;
  font-size: 14px;
  line-height: 20px;
}

.search-form__input:focus {
  color: var(--main-color);
  background: linear-gradient(#fff, #f6f6f7);
  border-color: #e1e7ed;
  font-weight: bold;
}

.search-form__content {
  filter: drop-shadow(0 1px 8px #3d4e611a);
  justify-content: center;
  align-items: center;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
}

.search-form__content:hover, .search-form__content--big:hover {
  filter: drop-shadow(0 4px 4px #00000040);
}

.search-form__content--small:hover {
  filter: drop-shadow(0 3px 8px #3d4e6133);
}

.search-logo {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
}

.search-logo--big {
  width: 19px;
  height: 19px;
  top: 25px;
  left: 36px;
}

.search-logo--small {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 23px;
}
